"use client"

import manageStore from '@/lib/store/store'
import React, { useEffect } from 'react'
import { useRouter } from 'next/navigation'
import Image from 'next/image'
import googlePng from "../../../public/google.png"

const page = () => {

  const { googleSignIn, isAuthenticated } = manageStore()

  const router = useRouter()

  const handleGoogleSignin = () => {
    googleSignIn()
  }

  useEffect(() => {
    if (isAuthenticated) {
      router.push("/")
    }
  }, [isAuthenticated])

  return (
    <div className=' pt-[20vh] h-[70vh]'>
      <h1 className='text-3xl uppercase text-center'>signin</h1>
      <div>
        <Image src="google.png" alt='google' priority width={20} height={20}/>
      </div>
    </div>
  )
}

export default page